.nav {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 20;
  display: flex;
  gap: 2.375rem;
  flex-direction: column;
  padding: 1.125rem;
}

.nav__btns {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1.125rem;
}

.nav__btn {
  position: relative;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  border: 1px solid #FFFFFF;
}

.nav__btns > .nav__btn {
  transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  opacity: 0;

  &:nth-child(1) {
    transition-duration: 780ms;
    transform: translate3d(0, 412px, 0);
  }

  &:nth-child(2) {
    transition-duration: 660ms;
    transform: translate3d(0, 314px, 0);
  }

  &:nth-child(3) {
    transition-duration: 540ms;
    transform: translate3d(0, 216px, 0);
  }

  &:nth-child(4) {
    transition-duration: 420ms;
    transform: translate3d(0, 118px, 0);
  }
}

.nav__btn--white {
  background-color: #FFFFFF;
}

.nav__btn--blur {
  background: linear-gradient(106.76deg, rgba(255, 255, 255, 0.35) 0%, rgba(255, 255, 255, 0.14) 100%);
  backdrop-filter: blur(25px);
}

.nav-btn__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
  pointer-events: none;

  &.hide {
    display: none;
  }
}

#closeBtn {
  position: relative;
  z-index: 10;
  display: none;

  &::before {
    content: "";
    position: absolute;
    top: -1.125rem;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    width: 2rem;
    height: 2px;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 10px;
  }
}

.show {
  display: flex !important;
}

.hide {
  display: none !important;
}

@media (max-width: 768px) and (orientation: portrait) {
  .nav {
    bottom: 0;
    left: 0;
    gap: 0.9375rem;
    padding: 11px 17px;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(45px);
  }

  .nav__btns {
    flex-direction: row;
    gap: 0.9375rem;
    justify-content: center;
  }

  .nav__btn {
    width: 4.625rem;
    height: 4.625rem;
  }

  .nav__btns > .nav__btn  {
    opacity: 1;
    transform: translate3d(0, 0, 0) !important;
  }

  #closeBtn, 
  #menuBtn {
    display: none !important;
  }
}

@media (max-width: 812px) and (orientation: landscape) {
  .nav {
    bottom: 50%;
    transform: translateY(50%);
  }

  .nav__btns {
    align-items: center;
  }
  
  .nav__btns > .nav__btn  {
    opacity: 1;
    transform: translate3d(0, 0, 0) !important;
  }

  .nav__btn {
    width: 4.625rem;
    height: 4.625rem;
  }

  #closeBtn, 
  #menuBtn {
    display: none !important;
  }
}
