.result {
  width: 460px;
  height: 460px;
  padding: 22px;
  margin: auto;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(45px);
  border-radius: 20px;
  overflow: hidden;

  &--placeholder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
}

.result__svg-placeholder {
  height: 400px;
}

.result__text-placeholder {
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.6);
  text-align: center;
}

.chat-container {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 303px 61px;
  gap: 32px;
  width: 100%;
}

.chat-messages-container {
  max-height: 280px;
  overflow-y: auto;

  /* width */
  &::-webkit-scrollbar {
    width: 3px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 10px;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: #FFFFFF;
    border-radius: 10px;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: #FFFFFF;
  }
}

.chat-message-wrapper {
  padding-bottom: 1rem;
}

.chat__message {
  font-size: 1rem;
  font-weight: 500;
  color: #000000;

  & > svg {
    display: inline-block;
    width: 13px;
    height: 10px;
    margin-left: .5rem;
  }
}

.settings_btn {
  display: none;
  width: 32px;
  height: 32px;
  padding: 5px;
}

.hide {
  display: none;
}

.card__title-w-btn {
  display: flex;
  gap: 18px;
  align-items: center;
}

.result__card-image {
  position: relative;
  width: 100%;
  height: 100%;
}

.card-image__img-wrapper {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;

  & > img {
    object-fit: cover;
  }
}

.card-image__footer {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: .5rem;
  width: 100%;
  padding: 22px;
}

.card-image__title {
  font-size: 18px;
  font-weight: bold;
  color: #FFFFFF;
}

.result__video-wrapper {
  position: relative;
  width: 232px;
  margin: auto;
}


.result__video {
  width: 100%;
  border-radius: 10px;
}

.result__video-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  width: 67px;
  height: 67px;
  border-radius: 50px;
  border: 1px solid #FFFFFF;
  background: linear-gradient(106.76deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0.24) 100%);
  backdrop-filter: blur(15px);

  & > svg {
    margin: auto;
  }
}

.card-title__link {
  display: none;
}

@media (max-width: 1152px) {
  .chat-container {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 166px 61px;
    gap: 32px;
    width: 100%;
  }

  .chat-messages-container  {
    height: 120px;
  }

  .result__svg-placeholder {
    height: 360px;
  }
}

@media (max-width: 812px) and (orientation: landscape) {
  .modal-2#chat {
    grid-template-columns: repeat(2, 260px);
    grid-template-rows: 57px 263px;
    grid-template-areas: "head head" "chat result";
    column-gap: 20px;
  }

  .modal-2__head {
    gap: 1rem;
  }

  .result {
    max-width: 260px;
    max-height: 260px;
  }

  .result .card__date {
    display: none;
  }

  .result .card__desc {
    display: none;
  }

  .result .card__link-container {
    display: none;
  }

  .result .card__title {
    margin-top: 8px;
    margin-bottom: 4px;
    font-size: 17px;
    height: 50px;

    & > .txt {
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      width: 178px;
    }
  }

  .card-title__link {
    display: block;
  }

  .result__video-wrapper {
    width: 120px;
  }
}

@media (max-width: 768px) and (orientation: portrait) {
  .settings_btn {
    display: block;
  }

  .settings {
    position: absolute;
    top: 42px;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 345px;
    padding: 20px 32px;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(52px);
    border: 1px solid #FFFFFF;

    & .toggle {
      width: 100%;
      justify-content: space-between;
    }
  }

  .result {
    max-width: 460px;
    max-height: 460px;
  }
}

@media (max-width: 500px) {
  .result {
    max-width: 345px;
    max-height: 345px;
  }

  .result .card__title {
    margin-top: 12px;
    margin-bottom: 4px;
    font-size: 17px;
  }

  .result .card__date {
    display: none;
  }

  .result .card__desc {
    line-height: 23px;
  }

  .result__article .card__title {
    margin-top: 0;
  }
  
  .result__video-wrapper {
    width: 170px;
  }
}

@media (max-width: 380px) {
  .result {
    max-width: 280px;
    max-height: 280px;
  }

  .result .card__desc {
    display: none;
  }

  .result .card__link-container {
    display: none;
  }

  .result__video-wrapper {
    width: 135px;
  }
}

