@use '../1-helpers/mixins' as mixins;

@include mixins.fontFace('/src/assets/fonts/montserrat/', 'Montserrat', 'regular', 'montserrat-regular');
@include mixins.fontFace('/src/assets/fonts/montserrat/', 'Montserrat', 'medium', 'montserrat-medium', 500);
@include mixins.fontFace('/src/assets/fonts/montserrat/', 'Montserrat', 'semi-bold', 'montserrat-semi-bold', 600);
@include mixins.fontFace('/src/assets/fonts/montserrat/', 'Montserrat', 'bold', 'montserrat-bold', 700);

.heading--1 {
  position: relative;
  max-width: 18.75rem;
  margin-bottom: 1.75rem;
  font-size: 1.875rem;
  font-weight: 700;

  &::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 2.8125rem;
    height: 2px;
    margin-top: 13px;
    background-image: url("/src/assets/images/heading-line.svg");
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.heading--2 {
  position: relative;
  max-width: 18.75rem;
  margin-bottom: 1.75rem;
  font-size: 1.5rem;
  font-weight: 700;

  &::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 2.8125rem;
    height: 2px;
    margin-top: 13px;
    background-image: url("/src/assets/images/heading-line.svg");
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.text--center {
  text-align: center;
}

.text--underline {
  text-decoration: underline;
}

.text--light {
  font-size: 14px;
  color: rgba(0, 0, 0, 50%);
}
