.modal {
  width: fit-content;
  padding: 2.5rem;
  background: linear-gradient(106.76deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0.24) 100%);
  backdrop-filter: blur(35px);
  border-radius: 20px;
}

.modal--hide {
  display: none !important;
}

.modal#searchResultsModal {
  overflow: hidden;
  padding-bottom: 0;

  & > .modal__body {
    overflow: auto;
    height: 630px;
    padding-right: 30px;

    /* width */
    &::-webkit-scrollbar {
      width: 6px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.2);
      border-radius: 10px;
      margin-bottom: 40px;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
      background: #FFFFFF;
      border-radius: 10px;
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
      background: #FFFFFF;
    }
  }
}

.modal__btn {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  width: 2.125rem;
  height: 2.125rem;
  margin: 10px;
  border-radius: 50%;
  background-color: #FFFFFF;
}

.modal-btn__icon {
  margin: auto;
}

.modal__text {
  font-size: 1rem;
  font-weight: 500;

  &--margin-bottom {
    margin-bottom: 1.75rem;
  }
}

.modal-2 {
  display: grid;
  grid-template-columns: repeat(2, minmax(460px, 100%));
  grid-template-rows: 58px minmax(auto, 402px);
  row-gap: 0;
  column-gap: 2rem;
  grid-template-areas: "head result" "chat result";

  & > .modal-2__head {
    grid-area: head;
  }

  & > .modal-2__col:nth-child(2) {
    grid-area: chat;
  }

  & > .modal-2__col:nth-child(3) {
    grid-area: result;
  }
}

.modal-2__head {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 173px 1px;
  justify-content: space-between;
}

@media (max-width: 768px) and (orientation: portrait) {
  .modal,
  .modal-2 {
    width: 100%;
    height: fit-content;
    min-height: 100%;
    padding: 30px 15px;
    border-radius: 0;
  }

  .modal-2 {
    justify-content: center;
    grid-template-columns: 100% !important;
  }

  .modal__btn {
    position: static;
    flex-basis: 2.125rem;
    flex-shrink: 0;
    margin: 0;
  }

  .modal__head {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: .5rem;
    width: 100%;
  }

  .modal-2__head {
    display: flex;
    gap: 10px;
    justify-content: normal;
    align-items: flex-start;

    & > .modal__btn-wrapper {
      flex-grow: 1;
      flex-shrink: 1;
      justify-self: end;
      align-self: flex-start;

      & > .modal__btn {
        margin-left: auto;
      }
    }
  }

  .modal#searchResultsModal {
    overflow: auto;
    padding-bottom: 0;
  
    & > .modal__body {
      overflow: visible;
      padding-right: 0;

      &::-webkit-scrollbar {
        width: 0;  /* Remove scrollbar space */
        height: 0;
        background: transparent;  /* Optional: just make scrollbar invisible */
      }
    }
  }
}

@media (max-width: 1152px) {
  .modal-2 {
    grid-template-columns: minmax(460px, 100%);
    grid-template-rows: 58px 280px 460px;
    grid-template-areas: "head" "chat" "result";
    column-gap: 0;
  }
}

@media (max-width: 812px) and (orientation: landscape) {
  .modal,
  .modal-2 {
    width: 100%;
    min-height: 100%;
    padding: 35px 88px;
    border-radius: 0;
  }

  .modal__btn {
    position: static;
    flex-basis: 2.125rem;
    flex-shrink: 0;
    margin: 0;
  }

  .modal__head {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: .5rem;
    width: 100%;
  }

  .modal#searchResultsModal {
    overflow: auto;
    padding-bottom: 0;
  
    & > .modal__body {
      overflow: visible;
      max-height: fit-content;
      padding-right: 0;
    }
  }
}

@media (max-width: 887px) {
  .modal--contacts {
    align-self: flex-start;
  }
}

