.btn-group {
  display: flex;
  gap: 1rem;
  margin-top: 2.375rem;
}

.btn {
  width: 100%;
  min-width: fit-content;
  height: 65px;
  padding: .5rem;
  border-radius: .5rem;
  border: 1px solid transparent;
  text-align: center;
}

.primary-btn {
  background-color: #FFFFFF;
}

.secondary-btn {
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 10%);
}
