.input-wrapper {
  width: 100%;
}

.input {
  display: block;
  width: 100%;
  padding: 25px 20px;
  border: 1px solid rgba(0, 0, 0, 10%);
  border-radius: 8px;
  background-color: transparent;

  &:focus {
    outline: 1px solid rgba(0, 0, 0, 1);
  }

  &::placeholder {
    font-size: 1rem;
    color: rgba(0, 0, 0, 50%);
  }
}

.input--error {
  position: relative;

  &::after {
    content: "Error";
    position: absolute;
    top: 26px;
    right: 19px;
    display: inline;
    font-size: 1rem;
  }

  & > .input,
  & > .textarea {
    padding-right: 4.6rem;
    border: 1px solid rgba(0, 0, 0, 1);
  }
}

.input--hide {
  position: absolute;
  z-index: -1;
  display: block;
  width: 1px;
  height: 1px;
  opacity: 0;
  overflow: hidden;
}

.textarea {
  display: block;
  width: 100%;
  height: 160px;
  min-width: 596px;
  padding: 25px 20px;
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 10%);
  border-radius: 8px;
  resize: none;

  &:focus {
    outline: 1px solid rgba(0, 0, 0, 1);
  }
}

.custom-file-input {
  display: flex;
  width: 288px;
  height: 146px;
  padding: 1rem;
  border: 1px dashed rgba(0, 0, 0, 10%);
  border-radius: .5rem;

  &--highlight {
    border: 1px dashed rgba(0, 0, 0, 100%);
  }
}

.custom-file-input__content {
  margin: auto;
}

.custom-file-input__icon {
  margin-bottom: .5rem;

  & > svg {
    margin: auto;
  }
}

.custom-file-input__text {
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

.file-input__btn {
  display: inline;
  cursor: pointer;
}

.input-btn-container {
  position: relative;
  width: 100%;
}

.input-btn__input {
  display: block;
  width: 100%;
  padding: 20px;
  border: 1px solid #FFFFFF;
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(45px);
}

.input-btn__btn {
  position: absolute;
  top: 50%;
  right: 6px;
  transform: translateY(-50%);
  width: 130px;
  height: 50px;
  background-color: #FFFFFF;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 8px;
  text-align: center;
}

.input-btn__btn--icon {
  display: none;
}

@media (max-width: 1366px) {
  .textarea {
    display: block;
    width: 100%;
    height: 160px;
    min-width: auto;
    padding: 25px 20px;
    background-color: transparent;
    border: 1px solid rgba(0, 0, 0, 10%);
    border-radius: 8px;
    resize: none;

    &:focus {
      outline: 1px solid rgba(0, 0, 0, 1);
    }
  }
}

@media (max-width: 1152px) {
  .input-btn__btn--txt {
    display: none;
  }

  .input-btn__btn.input-btn__btn--icon {
    display: flex;
    width: 53px;
    height: 53px;

    & > svg {
      margin: auto;
    }
  }
}

@media (max-width: 375px) {
  .custom-file-input {
    width: 100%;
  }
}
