.search-wrapper {
  position: relative;
}

.search {
  position: relative;

  &--show-autocomplete {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      width: 80%;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.06);;
    }
  }
}

.search__input {
  display: block;
  width: 550px;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 54px;
  padding-right: 142px;
  border: 1px solid #FFFFFF;
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(45px);

  &:focus {
    outline: 1px solid rgba(0, 0, 0, 1);
  }

  &::placeholder {
    font-size: 1rem;
    color: rgba(0, 0, 0, 50%);
  }


  &--show-autocomplete {
    border-bottom: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  &--close-btn {
    width: 100%;
    padding-right: 176px;
  }
}

.search__icon {
  position: absolute;
  top: 50%;
  left: 22px;
  transform: translateY(-50%);
}

.search__btn {
  position: absolute;
  top: 50%;
  right: 6px;
  transform: translateY(-50%);
  width: 130px;
  height: 50px;
  background-color: #FFFFFF;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 8px;
  text-align: center;
}

.search__btn--icon {
  position: absolute;
  top: 50%;
  right: 6px;
  transform: translateY(-50%);
  display: none;
  width: 50px;
  height: 50px;
  background-color: #FFFFFF;
  border-radius: 8px;

  & > svg {
    margin: auto;
  }
}

.search__autocomplete-container {
  position: absolute;
  z-index: 10;
  width: 100%;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(45px);
  border-bottom-left-radius: 14px;
  border-bottom-right-radius: 14px;
  border-left: 1px solid #FFFFFF;
  border-right: 1px solid #FFFFFF;
  border-bottom: 1px solid #FFFFFF;
}

.search__link {
  display: block;
  width: 100%;
  padding: .5rem;
}

.search__close-btn {
  position: absolute;
  top: 50%;
  right: 136px;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  border-radius: 50px;

  & > svg {
    margin: auto;
  }
}

.search__input--close-btn {
  padding-right: 190px;
}

.search-result {
  padding: 2rem 0;
}

.search-result__text {
  padding-bottom: 10px;
  margin-bottom: 30px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
}

.search-result__cards {
  display: flex;
  gap: 30px;
}

.search-result__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

@media (max-width: 1366px) {
  .search-result__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 812px) and (orientation: landscape) { 
  .search__input {
    width: 100%;
  }
}

@media (max-width: 768px) and (orientation: portrait) {
  .search__input {
    width: 100%;
    padding-right: 0;
  }

  .search__btn {
    position: static;
    width: 100%;
    margin-top: 10px;
    transform: translateY(0);
  }

  .search__btn--results {
    display: none;
  }

  .search__close-btn {
    right: 6px;
  }

  .search__close-btn--results {
    right: 62px;
  }

  .search__btn--icon  {
    display: flex;
  }

  .search__input--close-btn {
    padding-right: 110px;
  }
}

@media (max-width: 530px) and (orientation: portrait) {
  .search-result__cards {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    justify-items: center;
  }
}

@media (max-width: 648px) and (orientation: landscape) {
  .search-result__cards {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    justify-items: center;
  }
}