.page-container {
  width: 100%;
  height: 100vh;
  background-image: url("/src/assets/images/background-img.png");
  background-color: #44526c;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.page {
  display: grid;
  grid-template-columns: 1fr 7.25rem;
  grid-template-rows: 100%;
  width: 100%;
  height: 100vh;
}

.page__modal {
  position: relative;
  align-self: center;
  justify-self: center;
  width: 100%;
  height: 100%;
  overflow-y: auto;

  display: flex;
  align-items: center;
  justify-content: center;
  touch-action: auto;
}

.page__nav {
  align-self: flex-end;
}

@media (max-width: 768px) and (orientation: portrait) {
  .page {
    grid-template-columns: 100%;
    grid-template-rows: 1fr 6rem;
  }

  .page__modal {
    display: block;
  }
}

@media (max-width: 812px) and (orientation: landscape) { 
  .page__nav {
    height: 100%;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(45px);
  }

  .page__modal {
    display: block;
  }
}
