@use "../1-helpers/variables" as var;

html, body {
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  font-family: var.$font-family;
  font-weight: 400;
  font-size: var.$global-font-size;
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 0;  /* Remove scrollbar space */
  height: 0;
  background: transparent;  /* Optional: just make scrollbar invisible */
}
