@mixin fontFace($path, $family, $type: '', $file, $weight: 400, $style: normal) {
    @font-face {
      font-family: $family;
        src: url('#{$path}#{$type}/#{$file}.eot'); /* IE9 Compat Modes */
        src: url('#{$path}#{$type}/#{$file}.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
            url('#{$path}#{$type}/#{$file}.woff2') format('woff2'), /* Super Modern Browsers */
            url('#{$path}#{$type}/#{$file}.woff') format('woff'), /* Pretty Modern Browsers */
            url('#{$path}#{$type}/#{$file}.ttf')  format('truetype'), /* Safari, Android, iOS */
            url('#{$path}#{$type}/#{$file}.svg##{$file}') format('svg'); /* Legacy iOS */
      font-weight: $weight;
      font-style: $style;
    }
}
