.contacts {
  display: flex;
  gap: 1.875rem;
}

.contacts-form-container {
  flex-basis: 676px;
}

.contacts__card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  width: 384px;
  padding: 18px;
  border: 1px solid rgba(0, 0, 0, 8%);
  border-radius: 9px;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.contacts-card__label {
  margin-bottom: 18px;
  font-size: 1rem;
  font-weight: 700;
}

.contacts-card__text {
  font-size: 1rem;
  word-break: break-all;
}

.contacts-card__icon {
  display: flex;
  width: 5.125rem;
  height: 5.125rem;
  border-radius: 8px;
  background-color: #FFFFFF;

  & > svg {
    margin: auto;
  }
}

.contacts__form {
  width: 100%;
  height: 100%;
  padding: 2.5rem;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(45px);
  border-radius: 14px;
}

.contacts__input-group {
  display: grid;
  grid-template-columns: repeat(2, 18.125rem);
  grid-template-rows: repeat(2, 72px);
  align-items: center;
  justify-items: center;
  gap: 1rem;
}

.contact-form__head {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.input-file-form {
  display: flex;
  gap: 20px;
}

.files-list-wrapper {
  min-width: 288px;
  flex-shrink: 0;
}

.files-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-height: 146px;
  overflow: auto;
}

.file-item-container {
  display: flex;
  align-items: center;
  gap: .3rem;
}

.file-data {
  display: flex;
  gap: .4rem;
}

.file__name {
  width: 163px;
  font-size: 14px;
  text-overflow: ellipsis;
  overflow: hidden; 
  white-space: nowrap;
}

.file__size {
  font-size: 14px;
  color: rgba(0, 0, 0, 50%);
}

.file__type-icon {
  padding-right: .5rem;
  flex-shrink: 0;
}

.file__remove-btn {
  margin-left: 15px;
  flex-shrink: 0;
}

@media (max-width: 1293px) {
  .contacts {
    flex-direction: column;
  }

  .contacts-form-container {
    flex-basis: fit-content;
  }
  
  .contacts-card-container {
    display: flex;
    gap: 1rem;
  }

  .contacts__card {
    flex-direction: column-reverse;
    align-items: flex-start;
    width: 214px;
    margin-bottom: 0 !important;
    gap: 2.2rem;
  }
}

@media (max-width: 887px) {
  .contacts-card-container {
    flex-direction: column;
  }

  .contacts__card {
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 100%;
  }

  .contacts__input-group {
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
    max-width: 678px;
  }

  .input-file-form {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .files-list-wrapper {
    min-height: 120px;
  }
}

@media (max-width: 690px) {
  .contacts__input-group {
    grid-template-columns: 100%;
    grid-template-rows: repeat(4, 72px);
  }
}

@media (max-width: 568px) {
  .contact-form__head {
    flex-direction: column-reverse;
    gap: 20px;
  }
}

@media (max-width: 410px) {
  .contacts__form {
    width: 100%;
    padding: 2.5rem 1.5rem;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(45px);
    border-radius: 14px;
  }
}

@media (max-width: 375px) {
  .file-data {
    flex-direction: column;
    gap: .1rem;
  }

  .files-list-wrapper {
    min-width: 100%;
    flex-shrink: 1;
  }

  .file-item-container {
    justify-content: space-between;
  }
}
