.card {
  max-width: 332px;
}

.card--chat-col-width {
  max-width: 416px;
  margin: auto;
}

.card__img-wrapper {
  max-width: 332px;
  border-radius: 10px;
  overflow: hidden;

  & > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.card__img-wrapper--chat-col-width {
  max-width: 416px;
  height: 210px;
}

.card__date {
  margin-top: 1rem;
  margin-bottom: .5rem;
  font-size: 14px;
  font-weight: 600;
  color: rgba(0, 0, 0, 50%);
}

.card__title {
  margin-bottom: 8px;
  font-size: 22px;
  font-weight: 700;
}

.card__desc {
  font-size: 14px;
  color: rgba(0, 0, 0, 80%);
  line-height: 25px;
}

.card__link-container {
  display: flex;
  gap: 12px;
  align-items: center;
  margin-top: .5rem;
}

.card__link {
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
}

.card__link-btn {
  display: flex;
  width: 34px;
  height: 34px;
  border-radius: 50px;
  background-color: #FFFFFF;

  & > svg {
    margin: auto;
  }
}

@media (max-width: 1152px) {
  .card__img-wrapper--chat-col-width {
    max-width: 416px;
    height: 155px;
  }
}
