.toggle-container {
  position: relative;
}

.toggle {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  gap: 10px;
  width: fit-content;
}

.toggle__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  opacity: 0;
  overflow: hidden;
}

.toggle__track {
  position: relative;
  display: block;
  width: 48px;
  height: 24px;
  background: rgba(0, 0, 0, 0.12);
  border: 4px solid rgba(0, 0, 0, 0.01);
  backdrop-filter: blur(45px);
  border-radius: 20px;
  box-sizing: content-box;
  cursor: pointer;
}

.toggle__indicator {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0%, -50%);
  display: block;
  width: 21px;
  height: 21px;
  background: #FFFFFF;
  border-radius: 50px;
  transition: all .15s ease-in-out;
}

.toggle__label {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.toggle__input:checked + .toggle__track > .toggle__indicator {
  transform: translate(126%, -50%);
}
