.rating__stars {
  position: relative;
  display: flex;
  gap: 0.5625rem;
  max-width: 17.625rem;
  padding-bottom: 2.5rem;

  &::before {
    content: "Bad";
    position: absolute;
    left: 0;
    bottom: 0;
    font-size: 0.875rem;
    color: rgba(0, 0, 0, .7);
  }

  &::after {
    content: "Very good!";
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: 0.875rem;
  }
}

.rating__star {
  position: relative;
  cursor: pointer;
}

.rating__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
}
